<template>
<div>
  <mu-table :fixedFooter="fixedFooter" :fixedHeader="fixedHeader" :height="height"
    :enableSelectAll="enableSelectAll" :multiSelectable="multiSelectable"
    :selectable="selectable" :showCheckbox="showCheckbox">
    <mu-thead slot="header">
      <mu-tr>
        <mu-th tooltip="考试名">考试名</mu-th>
        <mu-th tooltip="科目名">科目名</mu-th>
        <mu-th tooltip="考场地址">考场地址</mu-th>
        <mu-th tooltip="日期">日期</mu-th>
        <mu-th tooltip="开始时间">开始时间</mu-th>
        <mu-th tooltip="结束时间">结束时间</mu-th>
        <mu-th>操作</mu-th>
      </mu-tr>
    </mu-thead>
    <mu-tbody>
      <mu-tr v-for="item,index in tableData"  :key="index" :selected="item.selected">
        <mu-td>{{item.exam_name}}</mu-td>
        <mu-td>{{item.sub_name}}</mu-td>
        <mu-td>{{item.room_add}}</mu-td>
        <mu-td>{{item.exam_date}}</mu-td>
        <mu-td>{{item.start}}</mu-td>
        <mu-td>{{item.over}}</mu-td>
        <mu-td @click="clickList(index)">
          <mu-flat-button primary @click="judgeIsTest(index)" label="参加"/>
        </mu-td>
      </mu-tr>
    </mu-tbody>
  </mu-table>
  <mu-dialog :open="dialog" title="考试密码" @close="dialog = false">
    <mu-text-field hintText="请输入考试密码" v-model="myPassword"/><br/>
    <mu-flat-button slot="actions" @click="dialog = false" primary label="取消"/>
    <mu-flat-button slot="actions" primary @click="joinTest()" label="确定"/>
  </mu-dialog>
</div>
</template>

<script>
export default {
  data () {
    return {
      tableData: [
        {
        }
      ],
      game1: 0,
      game2: ['1'],
      game3: '3',
      list: ['数据库', '计算机组成原理', '算法设计与分析', '无线传感器', '计算机操作系统', '软件工程'],
      fixedHeader: true,
      fixedFooter: false,
      selectable: false,
      multiSelectable: false,
      enableSelectAll: false,
      showCheckbox: false,
      height: '500px',
      dialog: false,
      myPassword: '',
      index: '',
      isTest: ''
    }
  },
  mounted: function(){
    this.getAddTestInfo()
  /*  var a = [1,5,9,6,7,15,14,19,23,53,83,96,97,98]
    this.selectList(13, a)*/
  },
  methods: {
    getAddTestInfo(){
      this.$ajax({
        method: 'get',
        url: 'http://47.94.254.3:3000/api/getAddTestInfo',
        params: {
          
        }
      })
      .then((response) => {
        //console.log(response.data.tableData)
        response = response.data.tableData 
        this.changeInfo(response)
        
      })
    },
    changeInfo(response){
      this.tableData = response
      console.log(this.tableData)
    },
    clickList (index) {
      //取到当前列
      this.index = index
    },
    joinTest(){
      //参加考试  
      this.dialog = false
      console.log(this.isTest)
      if(this.isTest){
        if(this.myPassword == this.tableData[this.index].exam_pw){
          //密码正确
          this.sessionSetter('exam_no', this.tableData[this.index].exam_no)
          this.$router.push('/front/regular')
        }else{
          alert('告诉你一个秘密，不知道密码考试就会挂。')
        }
      }else{
        alert('考过就不要再考人生已太多烦恼')
      }
    },
    judgeIsTest(index){
      this.dialog = true
      //console.log(parseInt(this.sessionGetter('stu_no')))
      //console.log(index)
      //console.log(this.tableData[index].exam_no)
      this.$ajax({
        method: 'post',
        url: 'http://47.94.254.3:3000/api/judgeIsTest',
        data: {
          exam_no: this.tableData[index].exam_no,
          stu_no: parseInt(this.sessionGetter('stu_no'))
        }
      })
      .then((response) => {
        console.log(response.data.tableData)
        if(response.data.tableData){
          //没有考过
          this.isTest = true
          return true
        }else{
          //考过
          this.isTest = false
          return false
        }
      })
    },
    sessionGetter(key){
      //取缓存内容
      const prefix = 'html5_test_'
      return sessionStorage.getItem(prefix + key);
    },
    sessionSetter(key, val){
      //设置缓存
      const prefix = 'html5_test_'
      sessionStorage.setItem(prefix + key, val)
    }
  }
}
</script>

<style lang="css">
.demo-table-settings{
  width: 200px;
  overflow: hidden;
  margin: 20px auto 0px;
}
</style>


