<template>
  <div class="layout">
    <div class="header">
      <img src="./../../static/img/logo.png">
      <div class="logo" @click="alert('hhh');">
        鹿角
      </div>
      
      <div class="nav">
        <mu-tabs :value="activeTab" @change="handleTabChange" class="tab">
          <mu-tab value="1" title="首页"/>
          <mu-tab value="2" title="视频换脸"/>
          <mu-tab value="3" title="实际项目"/>
          <mu-tab value="4" title="联系我们"/>
        </mu-tabs>
      </div>
    </div>
    <div class="content">
      <div class="body">
        <div class="contantGrid">
          <mu-row gutter>
            <mu-col width="50" tablet="30" desktop="33" class="columnShow">
              <mu-card>
                <mu-card-media title="人工智能">
                  <img src="./../../static/img/intelligence.jpg" class="animated pulse"/>
                </mu-card-media>
                <mu-card-title title="人工智能政策支持"/>
                <mu-card-text>
                  人们对实现智能生活一直充满期待，人工智能也从未停止发展。阿尔法狗两次战胜人类，更是掀起了人工智能到底能不能取代人类这个问题讨论的狂潮，谁也没有答案，谁也不敢真正断言。毋庸置疑，现在人工智能的发展已经开启了新阶段，不仅仅再只是存在于实验室中的机器人，而是慢慢向大众生活贴近，融入日常生活中。高铁站开始实行人脸识别的安检通道，siri更是个人助理般的存在，连我们拍照常用的面部贴纸也有与人工智能相关。可见，人工智能不但只是为了便利生活而存在，还能使我们生活过的更有意思。
                </mu-card-text>
                <!-- <mu-card-actions>
                  <mu-flat-button label="Action 1"/>
                </mu-card-actions> -->
              </mu-card>
            </mu-col>
            <mu-col width="50" tablet="30" desktop="33" class="columnShow">
              <mu-card>
                <mu-card-media title="短视频">
                  <img src="./../../static/img/shortVideo.jpg" />
                </mu-card-media>
                <mu-card-title title="短视频领域发展"/>
                <mu-card-text>
                  发展越来越快，生活节奏也越来越快，使得娱乐也更碎片化。为了适应这种模式，近两年许多短视频社区APP相继推出，并掀起热潮。新形式的分享生活方式，因新鲜感吸引的用户也很容易因此消散。已有app模板的局限性和视频库的不足，很容易使用户审美疲劳，并且因为固定的模板，用户参与感低、创作空间小，这些弊端在后期会使用app只会是昙花一现的存在。
                </mu-card-text>
                <!-- <mu-card-actions>
                  <mu-flat-button label="Action 1"/>
                </mu-card-actions> -->
              </mu-card>
            </mu-col>
            <mu-col width="50" tablet="20" desktop="33" class="columnShow">
              <mu-card>
                <mu-card-media title="鹿角">
                  <img src="./../../static/img/deerDes.jpg" />
                </mu-card-media>
                <mu-card-title title="鹿角功能特点"/>
                <mu-card-text>
                  鹿角不但包含有传统的视频剪辑功能，还包含有录屏功能，让用户能随时保存并编辑自己所喜爱的视频片段。更有创新性的视频换脸功能，趣味性十足。在视频里，你就是热门剧的主角，满足你和喜欢的偶像同框演戏的愿望。这项功能还可以应用在影视行业，一部剧主角的选取总是要经过好几轮试镜，多方配合，不但耗费人力物力，有时还达不到预期效果。在换脸功能的支持下，导演对某个角色的演员选取的过程中，能提前进行预判，先通过APP进行粗筛选后再进行小规模试镜。除此之外，我们的app还能帮用户更了解自己的喜好，根据用户在社区内的上传和浏览视频记录，自动生成用户画像，并根据画像为用户智能分发内容，让用户每次都能看到最感兴趣的内容，在使用app过程中永远不会觉得无聊，每次使用都有新的惊喜。
                </mu-card-text>
                <!-- <mu-card-actions>
                  <mu-flat-button label="Action 1"/>
                </mu-card-actions> -->
              </mu-card>
            </mu-col>
          </mu-row>

        </div>
      </div>
    </div>
    <div class="footer">
      蚁客未来 ©2018 Created
    </div>

  </div>
</template>
<script>
export default {
  data () {
    return {
      activeTab: '1'
    }
  },
  methods: {
    handleTabChange (val) {
      this.activeTab = val
    }
  }
}
</script>
<style scoped>
.layout{
  background-color: rgb(236, 236, 236);
}

.header{
  background-color: #009688;
  margin-bottom: 40px;
  height: 80px;
}

.logo{
  font-size: 24px;
  color: white;
  display: inline-block;
  padding: 10px 20px;
}

.nav{
  display: inline-block;
  width: calc(100% - 150px);
  margin: 0 auto;
}

.tab{
  margin: 0 auto;
  width: 400px;
  background-color: rgba(0, 0, 0, 0);
}

.content{
  width: 90%;
  margin: 0 auto;
}

.breadcrumb{
  margin: 10px 0;
}

.body{
  background-color: white;
  border-radius: 5px;
  min-height: 650px;
}
.footer{
  padding: 20px 0;
  text-align: center;
}
.logo{
  line-height: 60px;
  position: relative;
  left: 90px;
}
.header img{
  width: 50px;
  position: relative;
  top: 16px;
  left: 98px;
}
.contantGrid{
  padding: 40px;
}
.columnShow{
  height: 600px;
}
</style>


