<template>
<div>
  <mu-float-button icon="add" mini style="margin: 5px; box-shadow:none; float: right;" @click="insertInfo()"/>
  <mu-dialog :open="dialog" title="请输入信息:" @close="dialog = false">
    <mu-text-field hintText="请输入最大容纳人数" v-model="addData.room_size"/><br/>
    <mu-text-field hintText="请输入地址" v-model="addData.room_add"/><br/>
    <mu-flat-button slot="actions" @click="dialog = false" primary label="取消"/>
    <mu-flat-button slot="actions" primary @click="insertSure()" label="确定"/>
  </mu-dialog>
  <mu-table :fixedFooter="fixedFooter" :fixedHeader="fixedHeader" :height="height"
    :enableSelectAll="enableSelectAll" :multiSelectable="multiSelectable"
    :selectable="selectable" :showCheckbox="showCheckbox">
    <mu-thead slot="header">
      <mu-tr>
        <mu-th tooltip="考生号">考场号</mu-th>
        <mu-th tooltip="密码">容纳人数</mu-th>
        <mu-th tooltip="姓名">地址</mu-th>
        <mu-th>操作</mu-th>
      </mu-tr>
    </mu-thead>
    <mu-tbody>
      <mu-tr v-for="item,index in tableData"  :key="index" :selected="item.selected">
        <mu-td>{{item.room_no}}</mu-td>
        <mu-td>{{item.room_size}}</mu-td>
        <mu-td>{{item.room_add}}</mu-td>
        <mu-td @click="clickList(index)">
          <mu-flat-button primary @click="deleteInfo(index)" label="删除"/>
        </mu-td>
      </mu-tr>
    </mu-tbody>
  </mu-table>
</div>
</template>

<script>
export default {
  data () {
    return {
      tableData: [
        {
        }
      ],
      fixedHeader: true,
      fixedFooter: false,
      selectable: false,
      multiSelectable: false,
      enableSelectAll: false,
      showCheckbox: false,
      height: '500px',
      dialog: false,
      addData:{
        room_size: '',
        room_add: ''
      }
    }
  },
  mounted: function(){
    this.$ajax({
      method: 'get',
      url: 'http://47.94.254.3:3000/api/getRoomInfo',
      params: {
        
      }
    })
    .then((response) => {
      console.log(response.data.tableData)
      response = response.data.tableData 
      this.changeInfo(response)
      
    })
  },
  methods: {
    changeInfo(response){
      this.tableData = response
      console.log(this.tableData)
    },
    clickList (index) {
      //取到当前列
      this.index = index
    },
    deleteInfo(index){
      //删除当前列的元素
      console.log(this.tableData[index].room_no)
      this.$ajax({
        method: 'post',
        url: 'http://47.94.254.3:3000/api/deleteRoomInfo',
        data: {
          room_no: this.tableData[index].room_no
        }
      })
      .then((response) => {
        response = response.data.tableData
        this.changeInfo(response)
      })
    },
    insertInfo(){
      this.dialog = true
    },
    insertSure(){
      this.$ajax({
        method: 'post',
        url: 'http://47.94.254.3:3000/api/addRoomInfo',
        data: {
        addData: this.addData
      }
      })
      .then((response) => {
        response = response.data.tableData
        console.log(response)
        this.changeInfo(response)
      })
      this.dialog = false
    }

  }
}
</script>

<style lang="css">
.demo-table-settings{
  width: 200px;
  overflow: hidden;
  margin: 20px auto 0px;
}
</style>


