<template>
<div>
  <mu-select-field v-model="game1" :labelFocusClass="['label-foucs']" label="选择添加科目">
    <mu-menu-item v-for="text,index in list" :key="index" :value="index" :title="text" />
  </mu-select-field>
  <mu-flat-button primary @click="testType = 1" label="选择题"/>
  <mu-flat-button primary @click="testType = 0" label="判断题"/>
  <mu-table :fixedFooter="fixedFooter" :fixedHeader="fixedHeader" :height="height"
    :enableSelectAll="enableSelectAll" :multiSelectable="multiSelectable"
    :selectable="selectable" :showCheckbox="showCheckbox">
    <mu-thead slot="header">
      <mu-tr>
        <mu-th tooltip="试题编号">试题编号</mu-th>
        <mu-th>查看</mu-th>
        <mu-th>删除</mu-th>
      </mu-tr>
    </mu-thead>
    <mu-tbody>
      <mu-tr v-for="item,index in tableData"  :key="index" :selected="item.selected">
        <mu-td>{{item.code}}</mu-td>
        <mu-td>
          <mu-flat-button primary @click="showProInfo(index)" label="查看"/>
        </mu-td>
        <mu-td>
          <mu-flat-button primary @click="deleteProInfo(testType,index)" label="删除"/>
        </mu-td>
      </mu-tr>
    </mu-tbody>
  </mu-table>
  <mu-dialog :open="showSelect" title="选择题题目" @close="showSelect = false">
    <p>题干： {{ selectInfo.question }}</p>
    <p>A: {{ selectInfo.cho_a }}</p>
    <p>B: {{ selectInfo.cho_b }}</p>
    <p>C: {{ selectInfo.cho_c }}</p>
    <p>D: {{ selectInfo.cho_d }}</p>
    <p>答案： {{ selectInfo.anwser }}</p>
    <mu-flat-button slot="actions" @click="showSelect = false" primary label="取消"/>
    <mu-flat-button slot="actions" primary @click="showSelect = false" label="确定"/>
  </mu-dialog>
  <mu-dialog :open="showJudge" title="判断题题目" @close="showJudge = false">
    <p>题干: {{ judgeInfo.question }}</p>
    <p>答案： {{ judgeInfo.anwser }}</p>
    <mu-flat-button slot="actions" @click="showJudge = false " primary label="取消"/>
    <mu-flat-button slot="actions" primary @click="showJudge = false" label="确定"/>
  </mu-dialog>
</div>
</template>

<script>
export default {
  data () {
    return {
      game1: 0,
      list: ['数据库', '计算机组成原理', '算法设计与分析', '无线传感器', '计算机操作系统', '软件工程'],
      tableData: [
        {
          code: '1505120206'
        }
      ],
      fixedHeader: true,
      fixedFooter: false,
      selectable: false,
      multiSelectable: false,
      enableSelectAll: false,
      showCheckbox: false,
      height: '500px',
      value: '1',
      testType: 1,
      nowTable: [],
      showSelect: false,
      showJudge: false,
      selectInfo: {
        question: '',
        cho_a: '',
        cho_b: '',
        cho_c: '',
        cho_d: '',
        anwser: ''
      },
      judgeInfo: {
        question: '',
        anwser: ''
      }
    }
  },
 mounted: function(){
    this.loadProInfo(this.game1, this.testType, -1)
  },
  watch: {
    testType: function(val){
      this.loadProInfo(this.game1, val, -1)
    },
    game1: function(val){
      this.loadProInfo(val, this.testType, -1)
    }
  },
  methods: {
    loadProInfo(proIndex, testType, index){
      //加载数据,判断类型，科目类别，和查询方式
      //
      //index为-1时查询所有，不然按index查询题目(后端实现)
      if(testType){
        //选择题
        this.$ajax({
        method: 'post',
        url: 'http://47.94.254.3:3000/api/getSelectInfo',
        data: {
          sub_no: proIndex + 2,
          cho_no: index
        }
        })
        .then((response) => {
          console.log(response.data.tableData)
          response = response.data.tableData 
          if(index < 0){
            this.changeInfoInTable(response, 1)
          }else{
            this.selectInfo = response[0]
            //待定
            this.showSelect = true

          }
        })
      }else{
        //判断题
        this.$ajax({
        method: 'post',
        url: 'http://47.94.254.3:3000/api/getJudgeInfo',
        data: {
          sub_no: proIndex + 2,
          tf_no: index
        }
        })
        .then((response) => {
          console.log(response.data.tableData)
          response = response.data.tableData 
           if(index < 0){
            this.changeInfoInTable(response, 0)
          }else{
            this.judgeInfo = response[0]
            //待定
            this.showJudge = true
          }
        })
      }
    },
    changeInfoInTable(response, reference){
      //改变展示
      this.nowTable = response
      this.tableData = []
      for (var i = 0; i < response.length; i++){
        this.tableData.push({})
        if(reference){
          //选择
          this.tableData[i].code = response[i].cho_no
        }else{
          //判断
          this.tableData[i].code = response[i].tf_no
        } 
      }
      console.log(this.tableData)
    },
    clickList (index) {
      //取到当前列
      this.index = index
    },
    deleteProInfo(proIndex, index){
      //删除信息
      if(proIndex){
        //选择题
        this.$ajax({
        method: 'post',
        url: 'http://47.94.254.3:3000/api/deleteSelectInfo',
        data: {
          cho_no: this.nowTable[index].cho_no
        }
        })
        .then((response) => {
          this.loadProInfo(this.game1, this.testType, -1)
          
        })
      }else{
        //判断题
        this.$ajax({
        method: 'post',
        url: 'http://47.94.254.3:3000/api/deleteJudgeInfo',
        data: {
          tf_no: this.nowTable[index].tf_no
        }
        })
        .then((response) => {
          this.loadProInfo(this.game1, this.testType, -1)
          
        })
      }
    },
    showProInfo(index){
      //展示题目
      this.loadProInfo(this.game1, this.testType, this.tableData[index].code)
    }

  }
}
</script>

<style lang="css">
.demo-table-settings{
  width: 200px;
  overflow: hidden;
  margin: 20px auto 0px;
}
</style>


