<template>
<div>
  <mu-float-button icon="add" mini style="margin: 5px; box-shadow:none; float: right;" @click="insertInfo()"/>
  <mu-dialog :open="dialog" title="请输入信息:" @close="dialog = false">
    <mu-text-field hintText="请输入姓名" v-model="addData.stu_name"/><br/>
    <mu-text-field hintText="请输入密码" v-model="addData.stu_pw"/><br/>
    <mu-text-field hintText="请输入性别" v-model="addData.stu_sex"/><br/>
    <mu-text-field hintText="请输入身份证号" v-model="addData.stu_id"/><br/>
    <mu-text-field hintText="请输入电话" v-model="addData.stu_tel"/><br/>
    <mu-flat-button slot="actions" @click="dialog = false" primary label="取消"/>
    <mu-flat-button slot="actions" primary @click="insertSure()" label="确定"/>
  </mu-dialog>
  <mu-table :fixedFooter="fixedFooter" :fixedHeader="fixedHeader" :height="height"
    :enableSelectAll="enableSelectAll" :multiSelectable="multiSelectable"
    :selectable="selectable" :showCheckbox="showCheckbox">
    <mu-thead slot="header">
      <mu-tr>
        <mu-th tooltip="考生号">考生号</mu-th>
        <mu-th tooltip="密码">密码</mu-th>
        <mu-th tooltip="姓名">姓名</mu-th>
        <mu-th tooltip="性别">性别</mu-th>
        <mu-th tooltip="联系方式">联系方式</mu-th>
        <mu-th tooltip="身份证">身份证</mu-th>
        <mu-th>操作</mu-th>
      </mu-tr>
    </mu-thead>
    <mu-tbody>
      <mu-tr v-for="item,index in tableData"  :key="index" :selected="item.selected">
        <mu-td>{{item.studentNumber}}</mu-td>
        <mu-td>{{item.password}}</mu-td>
        <mu-td>{{item.name}}</mu-td>
        <mu-td>{{item.sexType}}</mu-td>
        <mu-td>{{item.phoneNumber}}</mu-td>
        <mu-td>{{item.idNumber}}</mu-td>
        <mu-td @click="clickList(index)">
          <mu-flat-button primary @click="deleteInfo(index)" label="删除"/>
        </mu-td>
      </mu-tr>
    </mu-tbody>
  </mu-table>
</div>
</template>

<script>
export default {
  data () {
    return {
      tableData: [
        {
          studentNumber: '1505120206',
          password: '123548',
          name: 'John Smith',
          sexType: '男',
          phoneNumber: '15727242025',
          idNumber: '421381199709269852'
        }
      ],
      fixedHeader: true,
      fixedFooter: false,
      selectable: false,
      multiSelectable: false,
      enableSelectAll: false,
      showCheckbox: false,
      height: '500px',
      dialog: false,
      addData:{
        stu_name: '',
        stu_pw: '',
        stu_sex: '',
        stu_id: '',
        stu_tel: ''
      }
    }
  },
  mounted: function(){
    this.$ajax({
      method: 'get',
      url: 'http://47.94.254.3:3000/api/getStudentInfo',
      params: {
        
      }
    })
    .then((response) => {
      response = response.data.tableData
      this.changeInfo(response)
    })
  },
  methods: {
    changeInfo(response){
      this.tableData = []
      for(var i = 0; i < response.length; i++){
        this.tableData.push({})
        this.tableData[i].studentNumber = response[i].stu_no
        this.tableData[i].password = response[i].stu_pw
        this.tableData[i].name = response[i].stu_name
        this.tableData[i].sexType = response[i].stu_sex
        this.tableData[i].phoneNumber = response[i].stu_tel
        this.tableData[i].idNumber = response[i].stu_id
      }
      console.log(this.tableData)
    },
    clickList (index) {
      //取到当前列
      this.index = index
    },
    deleteInfo(index){
      //删除当前列的元素
      this.$ajax({
        method: 'post',
        url: 'http://47.94.254.3:3000/api/deleteStudentInfo',
        data: {
          stu_no: this.tableData[index].studentNumber
        }
      })
      .then((response) => {
        response = response.data.tableData
        this.changeInfo(response)
      })
    },
    insertInfo(){
      this.dialog = true
    },
    insertSure(){
      this.$ajax({
        method: 'post',
        url: 'http://47.94.254.3:3000/api/addStudentInfo',
        data: {
        test: 'test',
        tests: ['1', '2'],
        addData: this.addData
      }
      })
      .then((response) => {
        response = response.data.tableData
        console.log(response)
        this.changeInfo(response)
      })
      this.dialog = false
    }

  }
}
</script>

<style lang="css">
.demo-table-settings{
  width: 200px;
  overflow: hidden;
  margin: 20px auto 0px;
}
</style>


