<template>
  <div>
    <ReadHeader/>
    <div id="pay-page">
      <div id="account" class="info">
        <span>账户: </span>
        <span class="infoContant">{{ userId }}</span>
      </div>
      <div id="balance" class="info">
        <span>余额: </span>
        <span class="infoContant">{{ balance }}猫币</span>
      </div>
      <div class="prompt">
        请选择充值方式:
      </div>
      <div>
        <div class="photoBorder" :class="{'payStyle': !payType}" @click="changePayType(0)">
          <div class="imgPositon">
            <img src="static/img/icon/alipay.png" class="iconPay">
          </div>
          <div class="textPay">
            <p>支付宝</p>
            <p class="textPromt">1元=100猫币</p>
          </div>
        </div>
        <div class="photoBorder" :class="{'payStyle': payType}" @click="changePayType(1)">
          <div class="imgPositon">
            <img src="static/img/icon/wechat.jpg" class="iconPay">
          </div>
          <div class="textPay">
            <p>微信</p>
            <p class="textPromt">1元=100猫币</p>
          </div>
        </div>
      </div>
      <div class="prompt">
        请选择支付金额:
      </div>
      <div class="payMoney">
        <button class="payNumber" @click="slectMoney(10)">10元</button>
        <button class="payNumber" @click="slectMoney(30)">30元</button>
        <button class="payNumber" style="margin-right: 0;" @click="slectMoney(50)">50元</button><br/>
        <button class="payNumber" @click="slectMoney(100)">100元</button>
        <mu-text-field hintText="最低充值一元" style="width: 62%;" type="number" :min="1" v-model="value"/>
      </div>
      <mu-raised-button label="提交" class="demo-raised-button" style="width: 66%; margin-top: 30px;" primary @click="sendPay()"/>
    </div>
    <ReadFooter/>    
  </div>
</template>

<script>
import ReadHeader from '@/components/pay/Header'
import ReadFooter from '@/components/pay/Footer'
export default {
  name: 'Home',
  data(){
    return{
      userId: '1505120206',
      balance: 50000,
      value: '',
      payType: 0
    }
  },
  methods: {
    slectMoney(val) {
      //绑定选择金额
      this.value = val
    },
    changePayType(val){
      this.payType = val

    },
    sendPay(){
      this.$router.push('/paySuccess')
    }
  },
  components: {
    ReadHeader,
    ReadFooter
  }
}
</script>

<style>
#home-page{
  width: 100%;
  font-size: 16px;
}

#search{
  padding-right: 16px;
}

.card-column{
  margin-top: 20px;
}
.info{
  margin: 16px;
  text-align: left;
  font-size: 16px;
}
.infoContant{
  padding-left: 2em;
}
.prompt{
  text-align: left;
  font-size: 16px;
  padding-left: 16px;
  line-height: 38px;
  height: 38px;
  width: 100%;
  background-color: #DDD;
}
.photoBorder{
  margin-top: 30px;
  margin-right: 3%;
  margin-left: 3%;
  width: 40%;
  height: 150px;
  display: inline-block;
  padding: 10px;
  border: 1px #DDD solid;
  margin-bottom: 60px;
}
.imgPositon{
  width: 30%;
  margin-left: 35%;
}
.iconPay{
  width: 100%;
}
.textPay{
  text-align: center;
  padding-right: 30px;
  width: 100%;
  height: 70px;
}
.textPay p{
  text-align: center;
}
.textPromt{
  color: #6d7358;
}
.payMoney{
  margin: 16px;
  margin-top: 30px;
  text-align: left;
}
.payNumber{
  width: 28%;
  margin-bottom: 16px;
  margin-right: 6%;
  border: 1px solid #d7c16b;
  background-color: #FFF;
  padding: 2px 4%;
}
.payStyle{
  background-color: #CCC
}
</style>



