<template>
	<div>
		<mu-sub-header>考场规则</mu-sub-header>
	  <mu-content-block>
	     <mu-list style="text-align: left;">
		    <mu-list-item title="1.   考生应带好二代身份证件及准考证（外国籍考生可用护照籍）。">
		      <mu-icon slot="right" value="info"/>
		    </mu-list-item>
		    <mu-list-item title="2.   考生务必准时进入考场，迟到者按放弃考试不得入场。">
		      <mu-icon slot="right" value="info"/>
		    </mu-list-item>
		    <mu-list-item title="3.   考生进入考场后须将身份证件放于桌面上方，以便检查。">
		      <mu-icon slot="right" value="info"/>
		    </mu-list-item>
		    <mu-list-item title="   4.   考生除身份证件和考试用品外，其它个人物品，应放置在考场内指定的位置。">
		      <mu-icon slot="right" value="info"/>
		    </mu-list-item>
		    <mu-list-item title="   5.    严禁将手机等电子设备带入考场。违者按作弊处理。">
		      <mu-icon slot="right" value="info"/>
		    </mu-list-item>
		    <mu-list-item title="   6.    考生应在答题纸上正确填写个人信息和答案，并承担填写错误的后果。">
		      <mu-icon slot="right" value="info"/>
		    </mu-list-item>
		    <mu-list-item title="   7.    请考生在考场内保持安静，不得交谈和走动。违规者，考试成绩将被取消。">
		      <mu-icon slot="right" value="info"/>
		    </mu-list-item>
		    <mu-list-item title="   8.    考试中途不得离开考场。否则考试成绩将被取消。">
		      <mu-icon slot="right" value="info"/>
		    </mu-list-item>
		    <mu-list-item title="   9.    在考试过程中有任何疑问等情况，请举手示意监考老师解决，不得大声喧哗。">
		      <mu-icon slot="right" value="info"/>
		    </mu-list-item>
		  </mu-list>
		  <mu-checkbox label="我已阅读考场规则" class="demo-checkbox" nativeValue="1" v-model="checkselect" style="text-align: center"/> <br/>
	  </mu-content-block>
	  <mu-raised-button label="同 意" class="demo-raised-button" :disabled="toNext" primary to="/front/doTest"/>
	</div>

</template>
<script>
	export default{
		data(){
			return{
				checkselect: '',
				toNext: true
			}
		},
		watch: {
			checkselect: function () {
				console.log(this.checkselect)
				if(this.checkselect == true){
					this.toNext = false
				}else{
					this.toNext = true
				}
			}
		},
		methods:{
			checkbox(val){
				console.log(val)
				//his.select = !this.select
			}
		}
	}
</script>
<style>
	.demo-raised-button{
		margin: 20px 0;
	}
</style>


