<template>
  <div class="layout">
    <div class="header">
      <div class="logo">
        网络在线考试后台
      </div>
    </div>
    <div class="content">
      <div class="content-left">
        <mu-list @change="handleListChange" :value="activeList">
          <mu-list-item title="学生信息" value="list1" to="/back/studentInfo/">
          </mu-list-item>
          <mu-list-item title="教室信息" value="list2" to="/back/roomInfo/">
          </mu-list-item>
          <mu-list-item title="添加试题" value="list3" to="/back/addProject/">
          </mu-list-item>
          <mu-list-item title="试题信息" value="list4" to="/back/projectInfo/">
          </mu-list-item>
          <mu-list-item title="考试结果" value="list5" to="/back/testResult/">
          </mu-list-item>
          <mu-list-item title="考试设置" value="list6" to="/back/addTest/">
          </mu-list-item>
          <mu-list-item title="信息维护" value="list7" to="/back/changePassword/">
          </mu-list-item>
        </mu-list>
      </div>
      <div class="content-right">
        <div class="breadcrumb">
          <mu-breadcrumb>
            <mu-breadcrumb-item href="#">用户ID： {{ userId }}</mu-breadcrumb-item>
            <mu-breadcrumb-item href="#">用户身份： {{ userType }}</mu-breadcrumb-item>
          </mu-breadcrumb>
        </div>
        <div class="body">
          <router-view></router-view>
        </div>
      </div>
    </div>
    <div class="footer">
      数据库课设 ©2017 Created by Muse-UI
    </div>
  </div>
</template>
<script>
export default {
  data () {
    return {
      activeTab: 'tab1',
      activeList: 'list1',
      userId: 'admin',
      userName: '姜太磊',
      userType: '管理员'
    }
  },
  methods: {
    handleTabChange (val) {
      this.activeTab = val
    },
    handleListChange (val) {
      this.activeList = val
    }
  }
}
</script>
<style scoped>
.layout{
  background-color: rgb(236, 236, 236);
}

.header{
  background-color: #7e57c2;
}

.logo{
  font-size: 24px;
  color: white;
  display: inline-block;
  padding: 10px 20px;
}

.nav{
  display: inline-block;
  width: calc(100% - 150px);
  margin: 0 auto;
}

.tab{
  margin: 0 auto;
  width: 400px;
  background-color: rgba(0, 0, 0, 0);
}

.content{
  overflow: hidden;
}

.content-left{
  width: 20%;
  float: left;
  background-color: white;
  margin-bottom: -4000px;
  padding-bottom: 4000px;
}

.content-right{
  width: 80%;
  display: inline-block;
  float: right;
  padding: 10px 20px;
  background-color: rgba(0, 0, 0, 0)
}

.breadcrumb{
  margin: 0 0 10px 0;
}

.body{
  background-color: white;
  border-radius: 5px;
  min-height: 500px;
}

.footer{
  padding: 20px 0;
  text-align: center;
}
</style>


