<template>
  <div class="layout">
    <div class="header">
      <div class="logo">
        溜飞天在线考试网
      </div>
    </div>
    <div class="content">
      <div class="body">
        <div style="padding-top:80px;">
          <mu-text-field label="账号" labelFloat v-model="stu_no"/><br/>
          <mu-text-field label="密码" hintText="请输入密码" type="password" labelFloa v-model="stu_pw"/><br/>
          <mu-radio label="管理登陆" name="group" nativeValue="1" v-model="selectVal" class="demo-radio"/> <br/>
          <mu-radio label="考生登陆" name="group" nativeValue="0" v-model="selectVal"  class="demo-radio"/> <br/>
          <mu-raised-button label="登 陆" class="demo-raised-button" primary style="margin-top: 40px;" @click="login" :to="link"/><br/>
          <mu-flat-button primary @click="dialog = true" label="注册"/>
        </div>
        <mu-dialog :open="dialog" title="注册" @close="dialog = false">
          <mu-text-field hintText="请输入姓名" v-model="addData.stu_name"/><br/>
          <mu-text-field hintText="请设置密码" v-model="addData.stu_pw"/><br/>
          <mu-radio label="男" name="group" nativeValue="男" v-model="addData.stu_sex" class="demo-radio"/> <br/>
          <mu-radio label="女" name="group" nativeValue="女" v-model="addData.stu_sex"  class="demo-radio"/> <br/>
          <mu-text-field hintText="请输入身份证号" v-model="addData.stu_id"/><br/>
          <mu-text-field hintText="请输入电话号码" v-model="addData.stu_tel"/><br/>
          <mu-flat-button slot="actions" @click="dialog = false" primary label="取消"/>
          <mu-flat-button slot="actions" primary @click="register()" label="确定"/>
        </mu-dialog>
      </div>
    </div>
    <div class="footer">
      数据库课设 ©2017 Created by Muse-UI
    </div>

  </div>
</template>
<script>
export default {
  data () {
    return {
      activeTab: 'tab1',
      userId: '1505120515',
      userName: '汤姆',
      sex: '男',
      selectVal: '0',
      link: '/front/regular',
      stu_no: '',
      stu_pw: '',
      dialog: false,
      addData: {
       stu_name: '',
       stu_pw: '',
       stu_sex: '',
       stu_id: '',
       stu_tel: ''
      }

    }
  },
  watch: {
    selectVal: function(val){
      //不同类型登陆选择 
      if(val == '1'){
        this.link = '/back/studentInfo'
      }
      if(val == '0'){
        this.link = '/front/historyGrade'
      }
    }
  },
  mounted(){
    this.sessionSetter('login', 'error')
    this.sessionSetter('manage', 'error')
  },
  methods: {
    register(){
      //注册
      var judgeNull = 0
      this.dialog = false
      for (var i in this.addData){
        if(!this.addData[i]){
          judgeNull++
        }
      }
      if(!judgeNull){
        this.$ajax({
          method: 'post',
          url: 'http://47.94.254.3:3000/api/register',
          data: {
            addData: this.addData
          }
        })
        .then((response) => {
          console.log(response.data.insertId)
          alert('您的账号是: ' + response.data.insertId + ' ，快拿小本本记下来')
          
        })
      }else{
        alert('传说注册信息不输完的人考试都挂了')
      }
 
    },
    login() {
      //登陆
      if(this.selectVal == '1'){
        //管理员登陆
        if(this.stu_pw && this.stu_no){
          this.$ajax({
            method: 'post',
            url: 'http://47.94.254.3:3000/api/manageLogin',
            data: {
              stu_no: this.stu_no,
              stu_pw: this.stu_pw
            }
          })
          .then((response) => {
            console.log(response)
            if(response.data.statue){
              this.sessionSetter('login', 'manage')
              this.$router.push('/back/studentInfo')
            }else{
              this.sessionSetter('login', 'error')
              alert('假的，都是假的，这双眼早已看透一切')
            }
            
          })
        }else{
          alert('不输入账号密码就想登陆，你这是为难我胖虎！')
        }
        
      }else{
        //考生登陆
        if(this.stu_pw && this.stu_no){
          this.$ajax({
            method: 'post',
            url: 'http://47.94.254.3:3000/api/stuLogin',
            data: {
              stu_no: this.stu_no,
              stu_pw: this.stu_pw
            }
          })
          .then((response) => {
            console.log(response)
            if(response.data.statue){
              this.sessionSetter('login', 'user')
              this.sessionSetter('stu_no', this.stu_no)
              this.$router.push('/front/selectSub')
            }else{
              this.sessionSetter('login', 'error')
              alert('假的，都是假的，这双眼早已看透一切')
            }
            
          })
        }else{
          alert('不输入账号密码就想登陆，你这是为难我胖虎！')
        }

      }
    },
    handleTabChange (val) {
      this.activeTab = val
    },
    sessionGetter(key){
      //取缓存内容
      const prefix = 'html5_test_'
      return sessionStorage.getItem(prefix + key);
    },
    sessionSetter(key, val){
      //设置缓存
      const prefix = 'html5_test_'
      sessionStorage.setItem(prefix + key, val)
    }
  }
}
</script>
<style scoped>
.layout{
  background-color: rgb(236, 236, 236);
}

.header{
  background-color: #7e57c2;
}

.logo{
  font-size: 24px;
  color: white;
  display: inline-block;
  padding: 10px 20px;
}

.nav{
  display: inline-block;
  width: calc(100% - 150px);
  margin: 0 auto;
}

.tab{
  margin: 0 auto;
  width: 400px;
  background-color: rgba(0, 0, 0, 0);
}

.content{
  width: 90%;
  margin: 0 auto;
}

.breadcrumb{
  margin: 10px 0;
}

.body{
  background-color: white;
  border-radius: 5px;
  min-height: 500px;
}

.footer{
  padding: 20px 0;
  text-align: center;
}
</style>


