<template>
<div>
  <mu-float-button icon="add" mini style="margin: 5px; box-shadow:none; float: right;" @click="insertInfo()"/>
  <mu-dialog :open="dialog" title="请输入信息:" @close="dialog = false">
    <mu-text-field hintText="请输入考试名" v-model="addData.exam_name"/><br/>
    <mu-text-field hintText="请输入考试密码" v-model="addData.exam_pw"/><br/>
    <mu-select-field v-model="game1" :labelFocusClass="['label-foucs']" label="选择考试科目">
      <mu-menu-item v-for="text,index in list" :key="index" :value="index" :title="text" />
    </mu-select-field><br/>
    <mu-text-field hintText="请输入考场号" v-model="addData.room_no" type="number"/><br/>
    <mu-text-field hintText="请输入考试日期" v-model="addData.exam_date"/><br/>
    <mu-text-field hintText="请输入考试开始时间" v-model="addData.start"/><br/>
    <mu-text-field hintText="请输入考试结束时间" v-model="addData.over"/><br/>
    <mu-text-field hintText="请输入选择题数" v-model="addData.selectNumber"/><br/>
    <mu-text-field hintText="请输入判断题数" v-model="addData.judgeNumber"/><br/>
    <mu-flat-button slot="actions" @click="dialog = false" primary label="取消"/>
    <mu-flat-button slot="actions" primary @click="insertSure()" label="确定"/>
  </mu-dialog>
  <mu-table :fixedFooter="fixedFooter" :fixedHeader="fixedHeader" :height="height"
    :enableSelectAll="enableSelectAll" :multiSelectable="multiSelectable"
    :selectable="selectable" :showCheckbox="showCheckbox">
    <mu-thead slot="header">
      <mu-tr>
        <mu-th tooltip="考试号">试卷号</mu-th>
        <mu-th tooltip="考试名">考试名</mu-th>
        <mu-th tooltip="密码">密码</mu-th>
        <mu-th tooltip="科目名">科目名</mu-th>
        <mu-th tooltip="考场地址">考场地址</mu-th>
        <mu-th tooltip="日期">日期</mu-th>
        <mu-th tooltip="开始时间">开始时间</mu-th>
        <mu-th tooltip="结束时间">结束时间</mu-th>
        <mu-th>操作</mu-th>
      </mu-tr>
    </mu-thead>
    <mu-tbody>
      <mu-tr v-for="item,index in tableData"  :key="index" :selected="item.selected">
        <mu-td>{{item.exam_no}}</mu-td>
        <mu-td>{{item.exam_name}}</mu-td>
        <mu-td>{{item.exam_pw}}</mu-td>
        <mu-td>{{item.sub_name}}</mu-td>
        <mu-td>{{item.room_add}}</mu-td>
        <mu-td>{{item.exam_date}}</mu-td>
        <mu-td>{{item.start}}</mu-td>
        <mu-td>{{item.over}}</mu-td>
        <mu-td @click="clickList(index)">
          <mu-flat-button primary @click="deleteInfo(index)" label="删除"/>
        </mu-td>
      </mu-tr>
    </mu-tbody>
  </mu-table>
</div>
</template>

<script>
export default {
  data () {
    return {
      tableData: [
        {
        }
      ],
      game1: 0,
      game2: ['1'],
      game3: '3',
      list: ['数据库', '计算机组成原理', '算法设计与分析', '无线传感器', '计算机操作系统', '软件工程'],
      fixedHeader: true,
      fixedFooter: false,
      selectable: false,
      multiSelectable: false,
      enableSelectAll: false,
      showCheckbox: false,
      height: '500px',
      dialog: false,
      addData:{
        exam_name: '',
        exam_pw: '',
        room_no: '',
        sub_no: 2,
        exam_date: '',
        start: '',
        over: '',
        selectNumber: '',
        judgeNumber: ''
      }
    }
  },
  mounted: function(){
    this.getAddTestInfo()
  /*  var a = [1,5,9,6,7,15,14,19,23,53,83,96,97,98]
    this.selectList(13, a)*/
  },
  methods: {
    getAddTestInfo(){
      this.$ajax({
        method: 'get',
        url: 'http://47.94.254.3:3000/api/getAddTestInfo',
        params: {
          
        }
      })
      .then((response) => {
        console.log(response.data.tableData)
        response = response.data.tableData 
        this.changeInfo(response)
        
      })
    },
    changeInfo(response){
      this.tableData = response
      console.log(this.tableData)
    },
    clickList (index) {
      //取到当前列
      this.index = index
    },
    deleteInfo(index){
      //删除当前列的元素
      console.log(this.tableData[index].exam_no)
      this.$ajax({
        method: 'post',
        url: 'http://47.94.254.3:3000/api/deleteAddTestInfo',
        data: {
          exam_no: this.tableData[index].exam_no
        }
      })
      .then((response) => {
        this.getAddTestInfo()
      })
    },
    insertInfo(){
      this.dialog = true
    },
    insertSure(){
      this.addData.room_no = parseInt(this.addData.room_no)
      this.addData.sub_no = this.game1 + 2
      var judgeNull = 0
      for (var i in this.addData){
        if(!this.addData[i]){
          judgeNull++
        }
      }
      if(!judgeNull){
        this.$ajax({
          method: 'post',
          url: 'http://47.94.254.3:3000/api/insertAddTestInfo',
          data: {
          addData: this.addData
          }
          })
          .then((response) => {
            this.getAddTestInfo()
          })      
        }else{
          alert('输入不能为空')
        }
        this.dialog = false
    },
    selectList(aim, nowList){
      var resultList = []
      if(aim > nowList.length){
        resultList = nowList
      }else{
        while(resultList.length < aim){
          var random = Math.floor(Math.random()*nowList.length)
          resultList.push(nowList[random])
          nowList.splice(random, 1)
        }
      }
      console.log(resultList)
      return resultList
    }

  }
}
</script>

<style lang="css">
.demo-table-settings{
  width: 200px;
  overflow: hidden;
  margin: 20px auto 0px;
}
</style>


