import Vue from 'vue'
import Router from 'vue-router'
import read from '@/components/read'
import testBackStage from '@/components/back/testBackStage'
import studentInfo from '@/components/back/studentInfo'
import roomInfo from '@/components/back/roomInfo'
import testResult from '@/components/back/testResult'
import changePassword from '@/components/back/changePassword'
import front from '@/components/front/testfront'
import regular from '@/components/front/regular'
import login from '@/components/login'
import selectSub from '@/components/front/selectSub'
import addProject from '@/components/back/addProject'
import projectInfo from '@/components/back/projectInfo'
import doTest from '@/components/front/doTest'
import pay from '@/components/pay/pay'
import paySuccess from '@/components/pay/paySuccess'
import addTest from '@/components/back/addTest'
import historyGrade from '@/components/front/historyGrade'
import antFuture from '@/components/antFuture'

Vue.use(Router)
const Bar = { template: '<div>bar</div>' }

export default new Router({
  routes: [
    {
      path: '/',
      name: 'antFuture',
      component: antFuture   
    },
    {
    	path: '/back',
    	component: testBackStage,
        beforeEnter: (to, from, next) => {
            const prefix = 'html5_test_'
            var val = sessionStorage.getItem(prefix + 'login');
            if(val == 'manage'){
                next()
            }else{
                next({path: '/login'})
            }
            
        },
    	children: [{
    		path: 'studentInfo',
    		component: studentInfo
    	},
    	{
    		path: 'roomInfo',
    		component: roomInfo
    	},
    	{
    		path: 'testResult',
    		component: testResult
    	},{
    		path: 'changePassword',
    		component: changePassword
    	},{
    		path: 'addProject',
    		component: addProject
    	},{
    		path: 'projectInfo',
    		component: projectInfo
    	},
        {
            path: 'addTest',
            component: addTest
        }
    	]
    },
    {
    	path: '/front',
    	component: front,
/*        beforeEnter: (to, from, next) => {
            const prefix = 'html5_test_'
            var val = sessionStorage.getItem(prefix + 'login');
            if(val == 'user'){
                next()
            }else{
                next({path: '/login'})
            }
        },*/
    	children: [{
    		path: 'regular',
    		component: regular
    	},
    	{
    		path: 'selectSub',
    		component: selectSub
    	},
        {
            path: 'doTest',
            component: doTest
        },
        {
            path: 'historyGrade',
            component: historyGrade
        }
    	]
    },
    {
    	path: '/login',
    	component: login
    },
    {
        path: '/pay',
        component: pay
    },
    {
        path: '/paySuccess',
        component: paySuccess
    }
  ]
})



