<template>
<div>
  <mu-text-field label="请输入旧密码" hintText="请输入密码" type="password" labelFloat v-model="oldPassword"/><br/>
  <mu-text-field label="新密码" hintText="请输入密码" type="password" labelFloat v-model="newPassworld"/><br/>
  <mu-text-field label="确认新密码" hintText="请输入密码" type="password" labelFloat v-model="aNewPassworld"/><br/>
  <mu-raised-button label="确认修改" class="demo-raised-button" style="margin-top: 50px;" @click="submitInfo()" primary/>
  <mu-popup position="top" :overlay="false" popupClass="demo-popup-top" :open="topPopup">
    两次输入不一致
  </mu-popup>
</div>
</template>

<script>
export default {
  data () {
    return {
      oldPassword: '',
      newPassworld: '',
      aNewPassworld: '',
      topPopup: false
    }
  },
  methods: {
    submitInfo(){
      if(this.newPassworld != this.aNewPassworld){
        //两次输入密码不一致
        this.topPopup = true
        setTimeout(() => {
          this.topPopup = false
        }, 2000)
      }else{
        //去修改密码
        this.$ajax({
          method: 'post',
          url: 'http://47.94.254.3:3000/api/modifyPw',
          data: {
            oldPw: this.oldPassword,
            newPw: this.newPassworld
          }
        })
        .then((response) => {
          response = response.data.tableData
          //console.log(response)
          if(response == 1){
            alert('密码修改成功')
          }else{
            alert('修改失败')
          }
        })
      }
    }
  }
}
</script>
<style>
  .demo-popup-top {
    width: 100%;
    opacity: .9;
    height: 48px;
    line-height: 48px;
    display: flex;
    align-items: center;
    justify-content: center;
    max-width: 375px;
  }
</style>


